import { DoublyLinkedList } from "./DoublyLinkedList";
import { BinarySearchTree } from "./BinarySearchTree";

const lista = new DoublyLinkedList<number>();
const arvore = new BinarySearchTree();
const output = document.getElementById("output")! as HTMLPreElement;

declare global {
  interface Window {
    // Lista
    addStart: () => void;
    addEnd: () => void;
    addAt: () => void;
    removeStart: () => void;
    removeEnd: () => void;
    removeAt: () => void;
    showForward: () => void;
    showBackward: () => void;
    checkEmpty: () => void;
    clearList: () => void;
    getSize: () => void;
    // √Årvore
    treeInsert: () => void;
    treeSearch: () => void;
    treeInOrder: () => void;
    treePreOrder: () => void;
    treePostOrder: () => void;
    treeLevelOrder: () => void;
    treeHeight: () => void;
    treeCount: () => void;
  }
}

// ----------------- Lista -----------------
window.addStart = () => {
  const value = getValue("valor");
  lista.insertAtStart(value);
  window.showForward();
};

window.addEnd = () => {
  const value = getValue("valor");
  lista.insertAtEnd(value);
  window.showForward();
};

window.addAt = () => {
  const value = getValue("valor");
  const pos = getValue("posicao");
  lista.insertAt(value, pos);
  window.showForward();
};

window.removeStart = () => {
  lista.removeFromStart();
  window.showForward();
};

window.removeEnd = () => {
  lista.removeFromEnd();
  window.showForward();
};

window.removeAt = () => {
  const pos = getValue("posicao");
  lista.removeFrom(pos);
  window.showForward();
};

window.showForward = () => {
  output.innerText = "Lista normal: " + lista.toArrayForward().join(" <-> ");
};

window.showBackward = () => {
  output.innerText = "Lista reversa: " + lista.toArrayBackward().join(" <-> ");
};

window.checkEmpty = () => {
  output.innerText = lista.isEmpty() ? "Lista vazia." : "Lista com elementos.";
};

window.clearList = () => {
  lista.clear();
  output.innerText = "Lista esvaziada.";
};

window.getSize = () => {
  output.innerText = `Tamanho da lista: ${lista.getSize()}`;
};

// ----------------- √Årvore -----------------
window.treeInsert = () => {
  const value = getValue("valor-arvore");
  arvore.insert(value);
  output.innerText = `Valor ${value} inserido na √°rvore.`;
};

window.treeSearch = () => {
  const value = getValue("valor-arvore");
  output.innerText = arvore.search(value)
    ? `Valor ${value} encontrado.`
    : `Valor ${value} n√£o encontrado.`;
};

window.treeInOrder = () => {
  output.innerText = "Em ordem (inOrder): " + arvore.inOrder().join(", ");
};

window.treePreOrder = () => {
  output.innerText = "Pr√©-ordem: " + arvore.preOrder().join(", ");
};

window.treePostOrder = () => {
  output.innerText = "P√≥s-ordem: " + arvore.postOrder().join(", ");
};

window.treeLevelOrder = () => {
  output.innerText = "Level-order (BFS): " + arvore.levelOrder().join(", ");
};

window.treeHeight = () => {
  output.innerText = `Altura da √°rvore: ${arvore.height()}`;
};

window.treeCount = () => {
  output.innerText = `Total de n√≥s: ${arvore.count()}`;
};

(window as any).treeAncestors = () => {
  const value = getValue("valor-consulta");
  const ancestors = arvore.ancestors(value);
  output.innerText = ancestors.length
    ? `Ancestrais de ${value}: ${ancestors.join(", ")}`
    : `Nenhum ancestral encontrado para ${value} ou n√≥ n√£o existe.`;
};

(window as any).treeDescendants = () => {
  const value = getValue("valor-consulta");
  const descendants = arvore.descendants(value);
  output.innerText = descendants.length
    ? `Descendentes de ${value}: ${descendants.join(", ")}`
    : `Nenhum descendente encontrado para ${value} ou n√≥ n√£o existe.`;
};

(window as any).treeLevel = () => {
  const value = getValue("valor-consulta");
  const level = arvore.getLevel(value);
  output.innerText = level >= 0
    ? `N√≠vel do n√≥ ${value}: ${level}`
    : `N√≥ ${value} n√£o encontrado na √°rvore.`;
};

(window as any).treeIsStrictlyBinary = () => {
  const result = arvore.isStrictlyBinary();
  output.innerText = `A √°rvore √© estritamente bin√°ria? ${result ? "Sim" : "N√£o"}`;
};

(window as any).treeIsFull = () => {
  const result = arvore.isFull();
  output.innerText = `A √°rvore √© cheia? ${result ? "Sim" : "N√£o"}`;
};

// ----------------- Util -----------------
function getValue(id: string): number {
  const input = document.getElementById(id) as HTMLInputElement;
  return parseInt(input.value);
}
